import { useEffect } from "react";
import "../scss/Information.scss";

const Information = () => {
  useEffect(() => {
    const infoTitle = document.querySelector('.information-title')! as HTMLElement;
    const infoDes = document.querySelector('.information-description')! as HTMLElement;
    
    
    const animHead = (heading: HTMLElement, delay: number) => {

      gsap.to(heading, {
          opacity: 1,
          duration: 0.2,
          delay: delay, // задержка для каждой буквы
          ease: "power1.out"
      });
  }
  function activeScrollTrigger(obj:HTMLElement, delay:number){
   
    obj.style.opacity = '0'
     
        ScrollTrigger.create({
            trigger: obj,
            start: `100 80%`,
            end: `200 5%`,
           /*  markers: true, */
            onEnter: ()=>{
                animHead(obj, delay)
            },
             
            
        });
  }
  const objEl = {
    title: [infoTitle, 0.5],
    des: [infoDes, 0.6],
 
}
// eslint-disable-next-line @typescript-eslint/no-unused-vars
Object.entries(objEl).forEach(([_, value]) => {
   
    const obj = value[0] as HTMLElement;     
    const num = value[1] as number;
    activeScrollTrigger(obj, num);
});


  }, []);
  return (
    <div className="information-container">
      <h1 className="information-title">Referrals</h1>
      <p className="information-description">
        Every meted friend you will get 50 tokens, and if your friends will
        stake tokens in staking, then you will get profit 5%
      </p>
    </div>
  );
};

export default Information;
