import { useEffect } from "react";
import "../scss/Balance.scss";

const data = [
  {
    quantity: "100,000",
    type: "Peexi Coin",
  },
  /* {
    quantity: "1000",
    type: "Peexi Token",
  }, */
];

const Balance = () => {
  useEffect(() => {
    const balanceContainer = document.querySelector('.balance-container')! as HTMLElement;
    const balanceTitle = document.querySelector('.balance-title')! as HTMLElement;
    const balanceItems = document.querySelectorAll<HTMLElement>('.balance-item')!;
    const animHead = (heading: HTMLElement, delay: number) => {

      gsap.to(heading, {
          opacity: 1,
          duration: 0.2,
          delay: delay, // задержка для каждой буквы
          ease: "power1.out"
      });
  }
    function activeScrollTrigger(obj:HTMLElement, delay:number){
   
      obj.style.opacity = '0'
       
          ScrollTrigger.create({
              trigger: obj,
              start: `100 80%`,
              end: `200 5%`,
             /*  markers: true, */
              onEnter: ()=>{
                  animHead(obj, delay)
              },
               
              
          });
    }
    balanceItems.forEach((el, num)=> {
      if(num === 0) {
        activeScrollTrigger(el, 0.7)
      }
      else activeScrollTrigger(el, (num + 1) / 2)
    })
    
    

  const objEl = {
    cont: [balanceContainer, 0.6],
    title: [balanceTitle, 0.7],
 
}
// eslint-disable-next-line @typescript-eslint/no-unused-vars
Object.entries(objEl).forEach(([_, value]) => {
   
    const obj = value[0] as HTMLElement;     
    const num = value[1] as number;
    activeScrollTrigger(obj, num);
});


  }, []);
  return (
    <div className="balance-container">
      <p className="balance-title">Balance</p>
      {data.map((item, index) => (
        <p key={index} className="balance-item">
          <span className="balance-amount">{item.quantity}</span> {item.type}
        </p>
      ))}
    </div>
  );
};

export default Balance;
