import { useEffect, useRef, useState } from "react";
import "../scss/Awards.scss";

const Awards = () => {
  const rewardsData = [
    { inviteText: "Invite 5 friends", points: "100", completed: true },
    { inviteText: "Invite 15 friends", points: "1000", completed: true },
    { inviteText: "Invite 50 friends", points: "3000", completed: false },
    { inviteText: "Invite 100 friends", points: "7000", completed: false },
    { inviteText: "Invite 500 friends", points: "20000", completed: false },
  ];

  const [isFixed, setIsFixed] = useState(false);
  const [isAbsolute, setIsAbsolute] = useState(false);
  const absolutePos = useRef(0);
  useEffect(() => {
    const updateHeight = () => {
      const balanceBox = document.querySelector('.balance-container') as HTMLElement;
      const awardBox = document.querySelector('.awards-container-main') as HTMLElement;

      if (balanceBox && awardBox) {
        const balanceHeight = balanceBox.offsetHeight;
        awardBox.style.height = `calc(100% - ${balanceHeight}px)`; // Use vh for responsiveness
        
      }
    };

    updateHeight(); // Initial calculation

    window.addEventListener('resize', updateHeight); // Add resize listener
    return () => window.removeEventListener('resize', updateHeight); // Cleanup
  }, []);
  useEffect(() => {
     
     
   
    const handleScroll = () => {
      if (window.innerWidth < 1000) return;

      const awardsCont = document.querySelector('.awards-container');
      const historyCont = document.querySelector('.referral-history');
      const balanceElement = document.querySelector('.balance-container');
      
      if (awardsCont && historyCont && balanceElement) {
        const historyContRect = historyCont.getBoundingClientRect();
        const awardsContRect = awardsCont.getBoundingClientRect();
        const balanceRect = balanceElement.getBoundingClientRect();

        // Check if balance is out of view
        if (balanceRect.bottom <= 0 && !isAbsolute) {
          setIsFixed(true);
        } else {
          setIsFixed(false);
        }
        console.log(historyContRect.bottom <= awardsContRect.bottom)
        if (historyContRect.bottom <= awardsContRect.bottom + 10 && isAbsolute === false) {     
           
            setIsAbsolute(true);
            if(absolutePos.current === 0){
              absolutePos.current = awardsContRect.bottom
            }
            const lastPos = awardsContRect.bottom            
            console.log('here', lastPos > absolutePos.current)
            if(lastPos > absolutePos.current){
              setIsAbsolute(false);
            }
            
          
          /* console.log(historyContRect.bottom <=  awardsContRect.bottom, 'absolute'); */
         
        } 
       
        /* if(balanceRect.bottom <= 0 &&  window.scrollY + 500 < awardsContRect.bottom + window.scrollY) {
          setIsAbsolute(false);
          setIsFixed(true);
        } */
         
      }
    }

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);
  useEffect(() => {
    const awardsTitle = document.querySelector('.awards-title')! as HTMLElement;
    const awardsDescription = document.querySelector('.awards-description')! as HTMLElement; 
    const rewardsTable = document.querySelector('.rewards-table')! as HTMLElement; 
    const animHead = (heading: HTMLElement, delay: number) => {

      gsap.to(heading, {
          opacity: 1,
          duration: 0.2,
          delay: delay, // задержка для каждой буквы
          ease: "power1.out"
      });
  }
  function activeScrollTrigger(obj:HTMLElement, delay:number){
   
    obj.style.opacity = '0'
     
        ScrollTrigger.create({
            trigger: obj,
            start: `100 80%`,
            end: `200 5%`,
           /*  markers: true, */
            onEnter: ()=>{
                animHead(obj, delay)
            },
             
            
        });
  }
  const objEl = {
    title: [awardsTitle, 0.8],
    des: [awardsDescription, 0.9],
    table: [rewardsTable, 1],
 
}
// eslint-disable-next-line @typescript-eslint/no-unused-vars
Object.entries(objEl).forEach(([_, value]) => {
   
    const obj = value[0] as HTMLElement;     
    const num = value[1] as number;
    activeScrollTrigger(obj, num);
});


  }, []);
  return (
    <div className="awards-container-main">
      <div className={`awards-container ${isFixed && !isAbsolute ? 'awardsFixed' : 'awardsNotFixed'} ${isAbsolute ? 'awardsAbsolute' : ''}`}>
        <h2 className="awards-title">Awards</h2>
        <p className="awards-description">
          You receive tokens in the amount of 5% of the tokens received for
          completing friends' quests. In the future, when your friends buy game
          items on the site and top up your wallet, you will receive 0.5% for your
          friends' transactions.
        </p>
        <div className="rewards-table">
          <ul className="rewards-list">
            {rewardsData.map((item, index) => (
              <li
                key={index}
                className={`reward-item ${item.completed ? "completed" : "not-completed"}`}
              >
                <span className="invite-text">{item.inviteText}</span>
                <span className="points">{item.points} PT</span>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Awards;
