import { useEffect, useState } from "react";
import "../scss/ReferralLink.scss";

const ReferralLink = () => {
  const referralLink = "PeexiPunkReferalLin...";
  const tokensFromFriends = 1100;
  const [copied, setCopied] = useState(false);
  const [position, setPosition] = useState({ x: 0, y: 0 });

 /*  const handleCopy = (
    
  ) => {
    navigator["clipboard"].writeText(referralLink).then(() => {
      setCopied(true);
      setTimeout(() => {
        setCopied(false);
      }, 2000);
    });
  }; */
  const handleCopy = () => {
     
      
      const tempInput = document.createElement('input');
      tempInput.value = referralLink;
      document.body.appendChild(tempInput);
      tempInput.select();
      document.execCommand('copy');
      document.body.removeChild(tempInput);
      setCopied(true);
      setTimeout(() => {
        setCopied(false);
      }, 2000);
    
  };

  const handleTouchStart = (event: React.TouchEvent<HTMLDivElement>) => {
    const target = event.target as HTMLDivElement | null;
    if (target) {
      const rect = target.getBoundingClientRect();
      const clientX = event.touches[0].clientX;
      const clientY = event.touches[0].clientY;
      setPosition({ x: clientX - rect.left + 20, y: clientY - rect.top + 20 });
    }
  };

  const handleClick = (event: React.MouseEvent<HTMLDivElement>) => {
    const target = event.target as HTMLDivElement | null;
    if (target) {
      const rect = target.getBoundingClientRect();
      const clientX = event.clientX;
      const clientY = event.clientY;
      setPosition({ x: clientX - rect.left  + 20, y: clientY - rect.top + 20});
    }
    handleCopy();
  };
  useEffect(() => {
    const referralTitle = document.querySelector('.referral-title')! as HTMLElement;
    const linkSection = document.querySelector('.share-link-section')! as HTMLElement;
    
    
    const animHead = (heading: HTMLElement, delay: number) => {

      gsap.to(heading, {
          opacity: 1,
          duration: 0.2,
          delay: delay, // задержка для каждой буквы
          ease: "power1.out"
      });
  }
  function activeScrollTrigger(obj:HTMLElement, delay:number){
   
    obj.style.opacity = '0'
     
        ScrollTrigger.create({
            trigger: obj,
            start: `100 80%`,
            end: `200 5%`,
           /*  markers: true, */
            onEnter: ()=>{
                animHead(obj, delay)
            },
             
            
        });
  }
  const objEl = {
    title: [referralTitle, 0.8],
    cont: [linkSection, 0.9],
 
}
// eslint-disable-next-line @typescript-eslint/no-unused-vars
Object.entries(objEl).forEach(([_, value]) => {
   
    const obj = value[0] as HTMLElement;     
    const num = value[1] as number;
    activeScrollTrigger(obj, num);
});


  }, []);
  return (
    <div className="referral-container">
      <h2 className="referral-title">Referral link</h2>

      <div className="share-link-section">
        <p className="share-link-title">Share link</p>
        <div
          className="link-box"
          onClick={handleClick}
          onTouchStart={handleTouchStart}
          onTouchEnd={handleCopy}
        >
          <span className="referral-link">{referralLink}</span>
          <button className="copy-button" aria-label="Copy link"></button>
          {copied && (
            <span
              className="copied-message"
              style={{ left: `${position.x}px`, top: `${position.y}px` }}
            >
              Copied
            </span>
          )}
        </div>
        <p className="tokens-title">Tokens from friends</p>
        <p className="tokens-amount">
          <span className="amount-highlighted">{tokensFromFriends}</span> Peexi
          Token
        </p>
      </div>
    </div>
  );
};

export default ReferralLink;
